<template>
  <div class="container mt-3">
    <loading :active.sync="isLoading" :is-full-page="true">
      <template slot="default">
        <i class="fas fa-spinner fa-pulse fa-5x" style="color:#F1B1C3;"></i>
      </template>
    </loading>
    <div class="row">
      <div class="col-md-8">
        <img
          :src="product.imageUrl"
          class="w-100"
          alt
        >
        <div class="productIntro">
          <h3></h3>
          <p>{{ product.description }}</p>
        </div>
        <div class="alert alert-secondary" role="alert">
          <h4 class="alert-heading text-center h2"></h4>
          <p class="pl-4">
            1
          </p>
          <hr class="border-white mt-0">
          <p>
            :
            <ol>
              <li>Amelie</li>
              <li>()</li>
              <li></li>
              <li>(Amelie)</li>
              <li></li>
            </ol>
          </p>
          <hr class="border-white mt-0">
          <p>
            
            <ol>
              <li></li>
              <li></li>
              <li></li>
            </ol>
          </p>
          <hr class="border-white mt-0">
          <p class="pl-4">
            <br>
            //<br>
            <br>
            <br>
            <br>
            
          </p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="sticky-top bg-pinkdark px-3 py-4">
          <h1 class="h2 pb-2 text-center text-white">
            {{ product.title }}
          </h1>
          <hr class="border-white mt-0">
          <div class="d-flex justify-content-end mb-3">
            <del class="text-muted align-self-end"> {{ product.origin_price | currency}}</del>
            <div class="h3 mb-0 ml-auto text-white">
              <small> NT</small>
              <strong>{{ product.price | currency}}</strong>
            </div>
          </div>
          <div class="input-group mb-2">
            <select class="form-control" v-model="product.buyNum">
              <option :value="num" v-for="num in 10" :key="num">{{ num }}{{ product.unit }}</option>
            </select>
          </div>
          <p class="text-right text-main font-weight-bold  mb-2">
						 {{product.buyNum*product.price | currency}}
					</p> 
          
          <a href="#" class="btn btn-block btn-lg btn-graylight font-weight-bold" @click.prevent="addtoCart(product.id, product.num)"></a>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      isLoading: false,
      productID: "",
      product: {}
    };
  },
  methods: {
    getProduct(id) {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMERPATH
      }/product/${id}`;
      this.$http.get(url).then(response => {
        if (response.data.success) {
          console.log(response.data);
          vm.product = response.data.product;
          vm.$set(vm.product,'buyNum',1);
          vm.isLoading = false;
        }
      });
    },
    addtoCart(id, qty=1) {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMERPATH
      }/cart`;
      const cart =  {
        product_id: id,
        qty,
      }
      this.$http.post(url, {data: cart}).then(response => {
        if (response.data.success) {
          vm.$bus.$emit('cart:update');
          this.$bus.$emit("messsage:push", response.data.message, "success");
          console.log(response.data);
          vm.isLoading = false;
        }
      });
    },
  },
  created() {
    const vm = this;
    vm.productID = vm.$route.params.id;
    vm.getProduct(vm.productID);
  },
};
</script>


