import Vue from 'vue';
import Router from 'vue-router';

/*  */
import Dashboard from '@/components/Dashboard';
import Products from '@/components/pages/Products';
import Orders from '@/components/pages/Orders';
import Coupons from '@/components/pages/Coupons';

/*  */
import Index from '@/components/Index';
import CustomerIndex from '@/components/pages/CustomerIndex';
import CustomerShopping from '@/components/pages/CustomerShopping';
import CustomerCart from '@/components/pages/CustomerCart';
import CustomerCheckorders from '@/components/pages/CustomerCheckorders';
import CustomerCheckout from '@/components/pages/CustomerCheckout';
import Product from '@/components/pages/Product';


Vue.use(Router)

export default new Router({
  // router
  scrollBehavior (to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition
		} else {
			return { x: 0, y: 0 }
		}
	},
  routes: [
    {
      path: '*',
      redirect: 'index'
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'products',
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true,
                  title: '' },
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: { requiresAuth: true,
                  title: '' },
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          meta: { requiresAuth: true,
                  title: '' },
        },
      ]
    },
    {
      path: '/',
      redirect: '/index',
      component: Index,
      children: [
        {
          path: 'index',
          name: '',
          component: CustomerIndex,
          meta: {
            title: ''
          }
        },
        {
          path: 'Shopping',
          name: '',
          component: CustomerShopping,
          meta: {
            title: ''
          }
        },
        {
          name:"",
          path: 'product/:id',
          component: Product,
          meta: {
            title: ''
          }
        },
        {
          path: 'cart',
          name: '',
          component: CustomerCart,
          meta: {
            title: ''
          }
        },
        {
          path: 'checkorders',
          name: '',
          component: CustomerCheckorders,
          meta: {
            title: ''
          }
        },
        {
          path: 'checkout/:orderId',
          name: '',
          component: CustomerCheckout,
          meta: {
            title: ''
          }
        }
      ]
    },
  ]
})



